import { ReactComponent as MicIcon } from '../../../../assets/images/mic.svg';
import { Button } from '../../../../components/Button';
import { MdSearch } from '@react-icons/all-files/md/MdSearch';
import { TypeAnimation } from 'react-type-animation';
import styled from 'styled-components';
import { CommunityPage, CommunityPageGDSCMGMCET, GDSCMGMCET, SearchBarLine } from '../../../../constants/constants';

function NavigationSection() {
  return (
    <Container>
      <NavbarContainer>
        <Navbar>
          <LeftEnd>
            <SearchIcon data-scroll data-scroll-position="top" />
            <StyledTypeAnimation sequence={[SearchBarLine]} speed={20} wrapper="div" />
          </LeftEnd>
          <RightEnd data-scroll-section>
            <MicIconImg data-scroll data-scroll-position="top" />
            <CameraImg
              src="data:image/svg+xml;base64,"
              alt="search by img"
              data-scroll
              data-scroll-position="top"
            />
          </RightEnd>
        </Navbar>
      </NavbarContainer>
      <ButtonContainer data-scroll-section>
        <a
          href={CommunityPageGDSCMGMCET}
          target="_blank"
          rel="noreferrer noopener"
        >
          <StyledButton data-scroll data-scroll-position="top">
            <ButtonSpan>
              <a href={CommunityPageGDSCMGMCET} target="_blank" className="href">
                Google Search
              </a>
            </ButtonSpan>
          </StyledButton>
        </a>
        <a
          href={CommunityPageGDSCMGMCET}
          target="_blank"
          rel="noreferrer noopener"
        >
          <StyledButton data-scroll data-scroll-position="top">
          <ButtonSpan>
              <a href={CommunityPageGDSCMGMCET} target="_blank" className="href">
                I'm Feeling Lucky
              </a>
            </ButtonSpan>
          </StyledButton>
        </a>
      </ButtonContainer>
    </Container>
  );
}

export default NavigationSection;

const Container = styled.div`
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;

  ${({ theme }) => theme.mobile`
    gap: 20px;
  `}
`;

const NavbarContainer = styled.div`
  width: 100%;
  padding: 0 auto;
  // background: white;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Navbar = styled.div`
  width: 700px;
  padding: 10px 20px;
  border: 1px solid #b6c0c9;
  border-radius: 24px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  ${({ theme }) => theme.tablet`
    width: 500px;
  `}

  ${({ theme }) => theme.mobile`
    width: 300px;
  `}
`;

const LeftEnd = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  cursor: text;
`;

const SearchIcon = styled(MdSearch)`
  font-size: 24px;

  color: #525151;

  ${({ theme }) => theme.mobile`
    font-size: 18px;
  `}
`;

const RightEnd = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;

  ${({ theme }) => theme.mobile`
    display: none;
  `}
`;

const ButtonContainer = styled.div`
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const StyledButton = styled(Button)`
  height: 36px;
`;

const ButtonSpan = styled.span`
  font-size: 14px;
`;

const MicIconImg = styled(MicIcon)`
  cursor: pointer;
`;

const CameraImg = styled.img`
  width: 24px;
  height: 24px;

  cursor: pointer;
`;

const StyledTypeAnimation = styled(TypeAnimation)`
  color: ${(props) => props.theme.backgroundColor.black};
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;

  ${({ theme }) => theme.mobile`
    font-size: 18px;
  `}
`;
